<template>
    <div class="news">
        <div class="filter-container">
            <el-input  style="width: 200px;" class="filter-item" placeholder="标题" v-model="advTi" id="sT">
            </el-input>
            <el-input  style="width: 200px;" class="filter-item" placeholder="公司名" v-model="cName" id="cName">
            </el-input>
             <el-date-picker
                        v-model="value4"
                        type="datetimerange"
                        :picker-options="pickerOptions2"
                        placeholder="选择有效期范围"
                        align="right">
             </el-date-picker>
            <el-button class="filter-item" type="primary"  icon="search" @click="goSearch(3)">搜索</el-button>
            <el-button class="filter-item" style="margin-left: 10px;" @click="handleCreate" type="primary" icon="edit">添加</el-button>
        </div>
        <div class="news-list">
            <el-table :data="advDatas" border fit highlight-current-row style="width: 100%">

                <el-table-column align="center" label="序号" width="65">
                    <template scope="scope">
                        <span>{{scope.row.id}}</span>
                    </template>
                </el-table-column>
                <el-table-column width="110px" align="center" label="公司名">
                    <template scope="scope">
                        <span style='color:red;'>{{scope.row.company}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="110px" label="标题">
                    <template scope="scope">
                        <span class="link-type">{{scope.row.ad_name}}</span>
                    </template>
                </el-table-column>

                <el-table-column width="300px" align="center" label="连接">
                    <template scope="scope">
                        <a :href="scope.row.ad_link">{{scope.row.ad_link}}</a>
                    </template>
                </el-table-column>
                <el-table-column width="110px" align="center" label="点击率">
                    <template scope="scope">
                        <span>{{scope.row.click_count}}</span>
                    </template>
                </el-table-column>

                <el-table-column class-name="status-col" label="有效期">
                    <template scope="scope">
                        <el-tag>{{scope.row.start_date}}</el-tag>至
                        <el-tag>{{scope.row.end_date}}</el-tag>
                    </template>
                </el-table-column>

                <el-table-column align="center" label="操作" width="150">
                    <template scope="scope">
                        <el-button size="small" type="success" @click="goEditPage(scope.row.id)">编辑
                        </el-button>
                        <el-button  size="small" type="danger" @click="delById(scope.row.id)">删除
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>
        <div class="block">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="pageSize"
                    layout="total, prev, pager, next, jumper"
                    :total="totalCount">
            </el-pagination>
        </div>
        <el-dialog
                :visible.sync="dialogVisible"
                size="tiny"
               >
            <span>确认删除吗？</span>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="delReal()">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>
<script>
    import {getAdvs} from '../../api/adv'
    import {delAdvById} from '../../api/adv'
    import {searchAdv} from '../../api/adv'
    export default{
        data(){
            return{
                id:-111,
                dialogVisible:false,
                advTi:"",
                cName:"",
                currentPage:1,
                pageSize:8,//每页显示的条目数
                totalCount:0,//总条数
                advDatas:[],
                pickerOptions2: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                value4: ''
            }
        },
        mounted(){
          this.$nextTick(function () {
             this.getAdvDatas(1,this.pageSize);
             this.initSearch();
          });
        },
        methods: {
            initSearch(){
                const _this = this;
                $('#sT').bind('input propertychange', function() {
                    _this.goSearch(1);
                });
                $('#cName').bind('input propertychange', function() {
                    _this.goSearch(2);
                });

            },
            goSearch(flag){
                let start_date = null;
                let end_date = null;
                if(flag ===1){
                    this.cName = null;
                }
                if(flag ===2){
                    this.advTi = null;
                }
                if(flag === 3){
                    this.advTi = null;
                    this.cName = null;
                    start_date = this.value4[0];
                    end_date = this.value4[1]
                }
                searchAdv(this.advTi,this.cName,start_date,end_date,0,this.pageSize).then((res)=>{
                    this.advDatas = [];
                    let tempArr = [];
                    if(res.status === 200){
                        tempArr = res.data.data.detail.hits.hits;
                        this.totalCount = res.data.data.detail.hits.total;
                    }
                    const len = tempArr.length;
                    if(len<=0){
                        this.getAdvDatas(1,this.pageSize);
                    }else{
                        for(let i = 0;i<len;i++){
                            tempArr[i]._source.id = tempArr[i]._id;
                            tempArr[i]._source.ad_name = tempArr[i]._source.title;
                            this.advDatas.push(tempArr[i]._source);
                        }
                    }

                },(err)=>{

                });
            },
            showMsg(str,flag){
                this.$message({
                    message:str,
                    type:flag ? "success":"warning"
                })
            },
            getAdvDatas(page,pageSize){
                getAdvs(page,pageSize).then((res)=>{
                    if(res.status === 200){
                        this.totalCount = res.data.data.total_count;
                        this.advDatas = res.data.data.detail;
                    }

                },(err)=>{

                });
            },
            handleCurrentChange(val) {
                this.getAdvDatas(val,this.pageSize);
            },

            //添加新闻
            handleCreate(){
                this.$router.push({path: '/adv/addadv', replace: true});
            },
            delReal(){
                this.dialogVisible = false;
                delAdvById(this.id).then((res)=>{
                 const _this = this;
                 _this.showMsg("删除成功!",true);
                 setTimeout(function () {
                 _this.$router.go(0);
                 },500)
                 },(err)=>{
                 this.showMsg("删除失败",false);
                 });
            },
            delById(id){
                this.dialogVisible = true;
                this.id = id;

            },
            goEditPage(id){
                this.$router.push({name: '编辑广告', params: {
                    advId: id,
                },replace: true})
            },
        },
    }
</script>
<style scoped>
    .filter-container{
        width: 100%;
        margin: 20px;
    }
    .block{
        width: 100%;
        margin: 20px auto;
    }
</style>


